.budget{
   padding: 15px;
   border-radius: 5px;
   background-color: var(--c2);
   height: 104px;
   width: 300px;
   min-width: 226px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   margin: 15px;
}

.budget span {
   font-size: 1.6rem;
   font-weight: 300;
}

.budget button {
   background-color: var(--c2);
   width: fit-content;
   cursor: pointer;
   font-weight: 500;
   font-size: 1.2rem;
   color: var(--c12);
   transition: 0.2s;
}

.budget button:hover,.budget button:focus{
   color: var(--c10);
}

.budget div {
   display: flex;
   flex-direction: row;
   width: 100%;
   justify-content: space-between;
   align-items: end;
}

.deleteButton {
   width: 40px;
   height: 20px;
   display: flex;
   justify-content: center;
   align-items: center;
}

.deleteButton {
   background-color: var(--c10);
   cursor: pointer;
}

.deleteButton svg {
   fill: var(--c4);
   transition: 0.2s;
}

.deleteButton svg:hover,
.deleteButton svg:focus {
   fill: var(--c3);
}

@media (max-width: 799px) {
   .budget{
      margin: 7.5px;
   }
}